// PortfolioCard.js
import React, { useContext } from "react";
import { AuthContext } from "../AuthContext";

const PortfolioCard = () => {
  const { isLoggedIn, user } = useContext(AuthContext);

  // Kim tra xem ngi dng c ng nhp hay khng
  if (!isLoggedIn) {
    return null; // Nu khng ng nhp, khng hin th g
  }

  return (
    <div className="portfolio">
      <div className="portfolio-card">
        <div className="portfolio-left">
          <div className="signature">{user.username || "Jane Doe"}</div>
        </div>
        <div className="portfolio-right">
          <h2>{user.username || "None"}</h2>
          <h4>{user.jobs || "None"}</h4> {/* Hin th chc danh nu c */}
          <ul>
            <li>{user.phone}</li>
            <li>{user.email}</li>
            <li>{user.address}</li>
          </ul>
          <button className="contact-btn">Contact</button>
        </div>
        <img
          src={user.image || "https://via.placeholder.com/150"} // Hin th nh ngi dng nu c
          alt="Profile"
          className="portfolio-image"
        />
      </div>
    </div>
  );
};

export default PortfolioCard;
