const Header = () => {
  const { isLoggedIn, user, logout } = useContext(AuthContext);
  const navigate = useNavigate();

  const handleSearch = (query) => {
    // X l logic tm kim, c th iu hng n trang kt qu tm kim hoc cp nht state
    console.log("Search query:", query);
    navigate(`/search?q=${query}`); // V d: iu hng n trang kt qu tm kim
  };

  const [isOpen, setIsOpen] = useState(false);

  const toggleDropdown = () => {
    setIsOpen(!isOpen);
  };

  return (
    <header>
      <div className="navbar">
        <div className="label">
          <div className="branch">
            <h1>Hung</h1>
          </div>
          <div className="search1">
            <SearchBar onSearch={handleSearch} /> {/* Thm SearchBar  y */}
          </div>
        </div>

        <div className="navbar-detail">
          <div className="menu">


            <div className="blog" onClick={() => navigate("/blog")}>
              Bi vit
            </div>

            
            <div className="postblog" onClick={() => navigate("/postblog")}>
              ng bi
            </div>

            <div
              className="dropdown"
              onBlur={toggleDropdown} // Khi dropdown mt focus, ng li
              tabIndex={0} // m bo dropdown c th nhn focus
            >
              <div className="dropdown-trigger" onClick={toggleDropdown}>
                Ch 
              </div>
              <div
                className="dropdown-content"
                style={{
                  maxHeight: isOpen ? "500px" : "0",
                  opacity: isOpen ? "1" : "0",
                }}
              >
                <Link to="/search?q=AI" onClick={toggleDropdown}>
                  AI
                </Link>
                <Link to="/search?q=WEB" onClick={toggleDropdown}>
                  WEB
                </Link>
                <Link to="/search?q=DEVOPS" onClick={toggleDropdown}>
                  DEVOPS
                </Link>
              </div>
            </div>

            <div className="home" onClick={() => navigate("/home")}>
              Trang ch
            </div>
          </div>

          <div className="account">
            {!isLoggedIn ? (
              <div className="login-buttons">
                <button className="button-62" onClick={() => navigate("/login")}>ng nhp</button>
                <button className="button-62" onClick={() => navigate("/register")}>ng k</button>
              </div>
            ) : (
              <div className="auth">
                <div className="dropdown">
                  <button
                    className="btn btn-secondary dropdown-toggle"
                    type="button"
                    id="dropdownMenuButton1"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    Dropdown button
                  </button>
                  <ul
                    className="dropdown-menu"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <li>
                      <Link className="dropdown-item" to="/portfolio">
                        Thng tin c nhn
                      </Link>
                    </li>
                    <li>
                      <Link className="dropdown-item" to="/blogposted">
                        Bi vit  ng
                      </Link>
                    </li>
                    <li>
                      <Link className="dropdown-item" to="/updateinfor">
                        Cp nht thng tin
                      </Link>
                    </li>
                  </ul>
                </div>
                <div className="name-account">
                  {user ? user.username : "User"}
                </div>
                <div className="avatar-account">
                  {user && user.image ? (
                    <img src={user.image} alt="" /> // Hin th avatar
                  ) : (
                    <i
                      className="bi bi-person-circle"
                      style={{ fontSize: "60px" }}
                    ></i>
                  )}
                </div>
                <button className="button-62" onClick={logout}>ng xut</button>
              </div>
            )}
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
