import React, { useState, useEffect } from "react";
import axios from "axios";
import { Carousel } from "react-bootstrap";
import Postcard from "./Postcard";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";

function Home() {
  const [blogs, setBlogs] = useState([]);

  const fetchBlogs = async () => {
    try {
      const response = await axios.get("http://localhost:8000/bloglist");
      // Ly 5 blog mi nht t d liu nhn c
      const recentBlogs = response.data.slice(0, 5);
      setBlogs(recentBlogs);
    } catch (error) {
      console.error("Error fetching blogs:", error);
    }
  };

  useEffect(() => {
    fetchBlogs(); // Gi API khi component c mount
  }, []);

  return (
    <div className="center">
      {/* React Bootstrap Carousel vi ni dung ng t blogs */}
      <Carousel>
        {blogs.map((blog, index) => (
          <Carousel.Item key={index}>
            <img
              className="d-block w-100"
              src={blog.image}
              alt={`Slide ${index + 1}`}
            />
            <Carousel.Caption>
              <h5>
                <strong style={{ fontSize: "30px" }}>{blog.title}</strong>
              </h5>
              <p>
                {blog.content.length > 200
                  ? `${blog.content.substring(0, 200)}...`
                  : blog.content}
              </p>
            </Carousel.Caption>
          </Carousel.Item>
        ))}
      </Carousel>

      {/* Hin th cc Postcard t blog */}
      <div
        style={{
          width: "100%",
          display: "grid",
          gridColumn: "span 3",
          gridTemplateColumns: "repeat(3, 1fr)",
          gap: "16px",
          padding: "16px",
          placeItems: "center",
          justifyContent: blogs.length % 3 === 1 ? "start" : "center", // Cn ton b li
        }}
      >
        {blogs.map((blog, index) => (
          <Postcard
            key={index}
            image={blog.image} // Truyn nh cho tng blog
            title={blog.title} // Truyn tiu 
            content={blog.content} // Truyn ni dung
            id={blog._id}
          />
        ))}
      </div>
    </div>
  );
}

export default Home;
