import React, { useState, useEffect } from "react";
import axios from "axios";

const Like = ({ postId, initialLikes, isLoggedIn, userId }) => {
  const [likesCount, setLikesCount] = useState(initialLikes); // S lng like ban u
  const [isLiked, setIsLiked] = useState(false);
  
  useEffect(() => {
    setIsLiked(false);
  }, [initialLikes, userId]);

  console.log(isLiked)

  const handleLike = async () => {
    if (!isLoggedIn) {
      alert("Bn cha ng nhp");
      return;
    }
    setIsLiked(!isLiked);
    setLikesCount((prevCount) => prevCount + (isLiked ? -1 : 1));
    const method = isLiked ? "PUT" : "POST";
    
    try {
      const response = await axios({
        method: method,
        url: `http://localhost:8000/${userId}/posts/${postId}/like`,
        data: {
          userId: userId,
          postId: postId
        },
      });

      // X l response nu cn thit
      if (response.status === 200) {
        // Nu phn hi thnh cng, c th cn x l thm d liu  y
        console.log("Like action success:", response.data);
        // V d: bn c th ly li s lng like t server nu mun ng b ha li
        // setLikesCount(response.data.likesCount);
      }

    } catch (error) {
      if (error.response) {
        // Li t phn hi ca server
        console.error("Li khi like bi vit:", error.response.data);
        console.error("M li HTTP:", error.response.status);
        console.error("Header ca phn hi:", error.response.headers);
      } else if (error.request) {
        // Khng nhn c phn hi t server
        console.error("Khng nhn c phn hi t server:", error.request);
      } else {
        // Li trong vic thit lp yu cu
        console.error("Li khi gi yu cu:", error.message);
      }
      // Quay li s lng like trc khi li xy ra
      setLikesCount((prevCount) => prevCount - (isLiked ? -1 : 1));
    }
  };

  return (
    <div style={{display: "flex", flexDirection: "row"}}>
      <div style={{display: "flex", alignItems: "center", padding: "10px"}}>{likesCount} Likes</div> 
      <button className="button button-like" onClick={handleLike}>
        <i className="fa fa-heart"></i>
        <span>Like</span>
      </button>
    </div>
  );
};

export default Like;
